.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  background-size: 35%;
  background-image: url("./images/dark-honeycomb.png");
}

#card-title {
  padding-left: 10px;
}

.app-img {
  height: 300px;
}

i.fa {
  padding-right: 5px;
  padding-left: 5px;
}

#resume {
  font-size: 12px;
  font-family: sans-serif;
  padding-left: 5px;
  padding-right: 5px;
}

.basic-contact {
  padding-left: 20px;
}
/* .sidenav-phone {
  padding-left: 35px;
} */

#contact {
  height: auto;
}

#contact-nav {
  display: inline-flex;
  flex-wrap: wrap;
}

.portfolioImages {
  max-width: 500px;
}

.portfolioLinks {
  color: antiquewhite;
}

#mugshot {
  max-width: 150px;
}
